import { ViewportScroller } from '@angular/common';
import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';
import { ModalDismissReasons, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Subject, takeUntil } from 'rxjs';
import { UserService } from 'src/app/core/user/user.service';
import Swal from 'sweetalert2';
import { VerManillaComponent } from './ver-manilla/ver-manilla.component';
import { environment } from 'src/environments/environment';
import { EditarManillaComponent } from './editar-manilla/editar-manilla.component';

@Component({
  selector: 'app-manillas-user',
  templateUrl: './manillas-user.component.html',
  styleUrls: ['./manillas-user.component.scss']
})
export class ManillasUserComponent implements OnInit, OnDestroy {

  @ViewChild("manillaNgForm") manillaNgForm: NgForm;
  @ViewChild("verManillaModal") VerManillaModal: VerManillaComponent;
  @ViewChild("editarManillaModal") EditarManillaModal: EditarManillaComponent;

  public closeResult: string;

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  manillasCount: number = 0;
  page: any = 1;

  user:any = {};

  types: any = [];

  loadingFields: boolean = false;

  fieldsList: any = [];

  manillaForm: FormGroup;

  maxDate:any = {};

  showAlert: boolean = false;
  messageAlert: string = "";

  disableButtons: boolean = false;

  manillasList: any = [];

  Toast:any;

  manillaSelected:any = {};
  limit: any = environment.pagination_limit;

  uploadedFiles:any = {};

  imgPreview: any = '';
  
  constructor(
    private _userService: UserService,
    private _changeDetectorRef: ChangeDetectorRef,
    private modalService: NgbModal,
    private _formBuilder: FormBuilder,
    private viewScroller: ViewportScroller
  ) { 
    let today = new Date();
    this.maxDate.year = today.getFullYear();
    this.maxDate.month = today.getMonth() + 1;
    this.maxDate.day = today.getDate();

    this.Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    })
  }

  ngOnInit(): void {

    this.manillaForm = this._formBuilder.group({
      tipo: ["", [Validators.required]],
      clienteCampos: this._formBuilder.array([]),
    });

    this._userService.userTypes$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((types: any) => {
        // Update the types list
        this.types = types;

        // Mark for check
        this._changeDetectorRef.markForCheck();
      });

    this._userService.userManillas$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((manillas: any) => {
        // Update the manillasList
        this.manillasList = this.orderDataManillas(manillas.misManillas);
        this.manillasCount = this.manillasList.length;
        // Mark for check
        this._changeDetectorRef.markForCheck();
      });

    // Subscribe to user changes
    this._userService.user$
    .pipe(takeUntil(this._unsubscribeAll))
    .subscribe((user: any) => {
        this.user = user;

        // Mark for check
        this._changeDetectorRef.markForCheck();
    });
    
  }

  /**
     * On destroy
     */
  ngOnDestroy(): void
  {
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next(null);
      this._unsubscribeAll.complete();
  }

  onFileSelected(event:any, fieldName:any, index:any){
    let fileSelected = event.target.files[0];
    if(fileSelected.type === 'image/jpeg' || fileSelected.type === 'image/png' || fileSelected.type === 'image/jpg' || fileSelected.type === 'application/pdf'){
      const reader = new FileReader();
      reader.readAsDataURL(fileSelected);
      reader.onload = () => {
        let dataN = reader.result.toString();
        let dataD = '';
        dataD = dataN.replace(/^data:image\/\w+;base64,/,'');
        this.imgPreview = dataN;
        this.uploadedFiles[fieldName] = dataD;
      };
    }else{
      this.Toast.fire({
        icon: "error",
        title: 'Debe subir un tipo de imagen valido (JPEG, JPG, PNG, PDF).',
      });
      this.uploadedFiles = {};
      this.manillaForm.get('clienteCampos')['controls'][index]['controls'][fieldName].setValue('');
      // this.manillaForm.get(fieldName).setValue('');
      fileSelected = '';
      this.imgPreview = '';
    }
  }

  orderDataManillas(data: any){
    let manillas:any = [];
    data.forEach(tipo => {
      manillas = [...manillas, ...tipo.manillas]
    });

    // data.forEach(tipo => {
    //   manillas[tipo._id] = tipo.manillas
    // });

    return manillas;
  }

  open(content) {
    this.modalService.open(content, {size: 'lg',ariaLabelledBy: 'modal-basic-title', backdrop: 'static', keyboard  : false, scrollable: true}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.getDismissReason();
    });
  }

  private getDismissReason() {
    this.fieldsList.forEach((element, index) => {
      (this.manillaForm.get("clienteCampos") as FormArray).removeAt(0);
    });
    this.fieldsList = [];
    this.manillaForm.get('tipo').setValue('');
  }

  //get manillas fields
  handleTypeChange(event: any) {
    this.fieldsList.forEach((element, index) => {
      (this.manillaForm.get("clienteCampos") as FormArray).removeAt(0);
    });
    this.fieldsList = [];
    if (event.target.value !== "") {
      this.loadingFields = true;
      this.getFieldsByType(event.target.value);
    }
  }

  getFieldsByType(type: any): void {
    this._userService.getFieldsByUserTypes(type).pipe(takeUntil(this._unsubscribeAll)).subscribe(
        (response: any) => {
          this.fieldsList = response;
          this.addFieldsToFormGroup(response);
          this.loadingFields = false;
        },
        (error) => {
          console.log(error);
        }
      );
  }

  addFieldsToFormGroup(fieldsList: any) {

    fieldsList.forEach((element) => {
      let campos: any = {};
      if(element.type === 'number' && element.required){
        campos[element.name] = ["", [Validators.required, Validators.pattern("[0-9]*")]];
      }

      if(element.type === 'number' && !element.required){
        campos[element.name] = ["", [Validators.pattern("[0-9]*")]];
      }

      if(element.required && element.type !== 'number'){
        campos[element.name] = ["", [Validators.required]];
      }

      if(!element.required && element.type !== 'number'){
        campos[element.name] = [""];
      }

      // Create an empty calle form group
      const clienteFormGroup = this._formBuilder.group(campos);

      // Add the calle form group to the phoneNumbers form array
      (this.manillaForm.get("clienteCampos") as FormArray).push(clienteFormGroup);
    });
    // Mark for check
    this._changeDetectorRef.markForCheck();
  }

  /**
   * Track by function for ngFor loops
   *
   * @param index
   * @param item
   */
  trackByFn(index: number, item: any): any {
    return item.id || index;
  }

  onSubmit(){
    this.showAlert = false;
    if(!this.manillaForm.disabled){
      if (!this.manillaForm.valid) {
        this.Toast.fire({
          icon: 'error',
          title: 'Debes completar todos los campos para solicitar una pulsera.'
        });
        // this.disableButton = true;
      } else {
        this.solicitarManilla();
      }
    }
    // Mark for check
    this._changeDetectorRef.markForCheck();
  }

  orderData(): any{
    let manilla = this.manillaForm.getRawValue();
    manilla.clienteCampos.forEach(element => {
      for (const [key, value] of Object.entries(element)) {
        manilla[key] = value;
      }
    });
    delete manilla.clienteCampos;

    this.fieldsList.forEach(field => {
      if(field.type === 'Date'){
        manilla[field.name] = this.fdnToString(manilla[field.name]);
      }
    });

    return manilla;
  }

  solicitarManilla(){
    // Do nothing if the form is invalid
    if (this.manillaForm.invalid) {
      return;
    }

    // Disable the form
    this.manillaForm.disable();

    let dataManilla = this.orderData();
    dataManilla.userId = this.user._id;
    dataManilla = {...dataManilla, ...this.uploadedFiles};

    //create manilla
    this._userService.crearSolicitudManilla(dataManilla).subscribe(
      (response) => {
        this.Toast.fire({
          icon: 'success',
          title: 'Pulsera solicitada con exito'
        });
        this.getManillasListUpdated();
        this.modalService.dismissAll();
        this.manillaForm.enable();
        this.manillaForm.reset();
      },
      (response) => {
        // Re-enable the form
        this.manillaForm.enable();
        // Set the alert
        // response.error.message.forEach(element => {
        //   this.messageAlert += element + ',';
        // });
        this.Toast.fire({
          icon: 'error',
          title: response.error.message
        });
        // this.messageAlert = response.error.message;
        this.viewScroller.scrollToPosition([0, 0]);
        // Show the alert
        // this.showAlert = true;
        // this.disableButton = true;
      }
    );
  }

  getManillasListUpdated(){
    this._userService.getUserManillasUpdate().pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this._userService.updateManillasList(response);
      },(error) => {
        console.log(error);
      }
    );
  }

  fdnToString(fecha:any):any{
    return `${fecha.year}-${fecha.month.toString().padStart(2, '0')}-${fecha.day.toString().padStart(2, '0')}`
  }

  abrirModalVerManilla(manilla: any){
    manilla.vista = 'ver';
    this._userService.updateManillaSelected(manilla);
    this.VerManillaModal.openModal();
  }

  abrirModalEditarManilla(manilla: any){
    manilla.vista = 'editar';
    this._userService.updateManillaSelected(manilla);
    this.EditarManillaModal.openModal();
  }

  onPageChange(event:any){
    // this.getManillasLisFiltered(((event - 1) * this.limit),tipo, estado);
  }
  pruebas(){
    // this.manillaForm.get('clienteCampos')['controls'][0]['controls']['foto_portador'].setValue('');
    // console.log(this.manillaForm.get('clienteCampos')['controls'][0]['controls']['foto_portador'].value);
    // console.log(this.manillaForm.controls.clienteCampos['controls']);
  }
}