import { ViewportScroller, isPlatformBrowser } from '@angular/common';
import { ChangeDetectorRef, Component, Inject, OnDestroy, OnInit, PLATFORM_ID, TemplateRef, ViewChild } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Gallery, GalleryItem, ImageItem, ImageSize, ThumbnailsPosition } from 'ng-gallery';
import { Lightbox } from 'ng-gallery/lightbox';
import { Subject, takeUntil } from 'rxjs';
import { UserService } from 'src/app/core/user/user.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-editar-manilla',
  templateUrl: './editar-manilla.component.html',
  styleUrls: ['./editar-manilla.component.scss']
})
export class EditarManillaComponent  implements OnInit, OnDestroy{

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  @ViewChild("modalEditarManilla", { static: false }) ModalEditarManilla: TemplateRef<any>;
  public closeResult: string;
  public modalOpen: boolean = false;

  //fields
  fieldsList:any = [];
  loadingFields: boolean = true;
  uploadedFiles:any = {};

  //manilla
  manilla: any = {};
  loading: boolean = true;
  @ViewChild("manillaNgForm") manillaNgForm: NgForm;
  manillaForm: FormGroup;
  maxDate:any = {};

  Toast:any;
  
  items: GalleryItem[];

  filesTemp: any = {}

  disableFields: boolean = false;

  constructor(
    @Inject(PLATFORM_ID) private platformId: Object,
        private router: Router, private modalService: NgbModal,
        private _formBuilder: FormBuilder,
        private _userService: UserService,
        private _changeDetectorRef: ChangeDetectorRef, public gallery: Gallery, public lightbox: Lightbox,
        private viewScroller: ViewportScroller
  ){
    let today = new Date();
    this.maxDate.year = today.getFullYear();
    this.maxDate.month = today.getMonth() + 1;
    this.maxDate.day = today.getDate();
    this.Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    })   
  }

  ngOnInit(): void {
    this.manillaForm = this._formBuilder.group({
      camposManilla: this._formBuilder.array([]),
    });

    this._userService.manillaSelected$
    .pipe(takeUntil(this._unsubscribeAll))
    .subscribe((manilla: any) => {
      this.loading = true;
      // Update the manilla list
      if(manilla !== null && manilla.vista === 'editar'){
        this.getManillaById(manilla.numid);
      }
      // console.log(this.manilla);

      // Mark for check
      this._changeDetectorRef.markForCheck();
    });
  }

  ngOnDestroy(): void {
    if(this.modalOpen){
      this.modalService.dismissAll();
    }
    // Unsubscribe from all subscriptions
    this._unsubscribeAll.next(null);
    this._unsubscribeAll.complete();
  }

  /**
   * Track by function for ngFor loops
   *
   * @param index
   * @param item
   */
  trackByFn(index: number, item: any): any {
    return item.id || index;
  }

  //Funciones modal
  openModal() {
    this.filesTemp = {};
    this.fieldsList = [];
    this.disableFields = false;
    this.modalOpen = true;
    if (isPlatformBrowser(this.platformId)) { // For SSR 
      this.modalService.open(this.ModalEditarManilla, { 
        size: 'lg',
        ariaLabelledBy: 'modal-basic-title',
        centered: true,
        windowClass: 'ModalEditarManilla' ,
        scrollable: true,
        keyboard: false,
        backdrop: 'static'
      }).result.then((result) => {
        `Result ${result}`
      }, (reason) => {
        this.getDismissReason();
      });
    }
  }

  private getDismissReason() {
    this.fieldsList.forEach((element, index) => {
      (this.manillaForm.get("camposManilla") as FormArray).removeAt(0);
    });
    this.fieldsList = [];
  }

  //Funciones manilla api
  getManillaById(id:any){
    this._userService.getManillaById(id).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this.manilla = response;
        this.getFieldsManillaByType(response.tipo);
      },(error) => {
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
      }
    );
  }

  getFieldsManillaByType(type: any): void {
    this._userService.getFieldsByUserTypes(type).pipe(takeUntil(this._unsubscribeAll)).subscribe(
        (response: any) => {
          this.fieldsList = response;
          this.fieldsList.forEach((element, index) => {
            (this.manillaForm.get("camposManilla") as FormArray).removeAt(0);
          });
          this.addFieldsToFormGroup(response);
          this.loadingFields = false;

        },
        (error) => {
          console.log(error);
        }
      );
  }

  editarManillaById(id:any){
    // Do nothing if the form is invalid
    if (this.manillaForm.invalid) {
      return;
    }

    this.disableFields = true;

    // Disable the form
    this.manillaForm.disable();

    let dataManilla = this.orderData();
    dataManilla = this.deleleteFileFields(dataManilla);
    dataManilla = {...dataManilla, ...this.uploadedFiles};

    this._userService.editarManillaById(id, dataManilla).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this.Toast.fire({
          icon: 'success',
          title: 'La informacion de la pulsera ha sido editada con exito.'
        });
        this.getManillasListUpdated();
        this.modalService.dismissAll();
      },(error) => {
        // Re-enable the form
        this.manillaForm.enable();
        // Set the alert
        // response.error.message.forEach(element => {
        //   this.messageAlert += element + ',';
        // });
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
        this.disableFields = false;
        // this.messageAlert = response.error.message;
        this.viewScroller.scrollToPosition([0, 0]);
        this._changeDetectorRef.markForCheck();
      }
    );
  }

  getManillasListUpdated(){
    this._userService.getUserManillasUpdate().pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this._userService.updateManillasList(response);
      },(error) => {
        console.log(error);
      }
    );
  }


  //Funciones form group
  addFieldsToFormGroup(fieldsList: any) {

    fieldsList.forEach((element) => {
      let campos: any = {};
      if(element.type === 'number' && element.required){
        campos[element.name] = [this.manilla[element.name], [Validators.required, Validators.pattern("[0-9]*")]];
      }

      if(element.type === 'number' && !element.required){
        campos[element.name] = [this.manilla[element.name], [Validators.pattern("[0-9]*")]];
      }

      if(element.required && element.type !== 'number' && element.type !== 'Date'){
        campos[element.name] = [this.manilla[element.name], [Validators.required]];
      }

      if(!element.required && element.type !== 'number' && element.type !== 'Date'){
        campos[element.name] = [this.manilla[element.name]];
      }

      if(element.required && element.type === 'Date'){
        campos[element.name] = [this.orderDate(element.name), [Validators.required]];
      }

      if(!element.required && element.type === 'Date'){
        campos[element.name] = [this.orderDate(element.name)];
      }

      // Create an empty calle form group
      const clienteFormGroup = this._formBuilder.group(campos);

      // Add the calle form group to the phoneNumbers form array
      (this.manillaForm.get("camposManilla") as FormArray).push(clienteFormGroup);
    });
    // Mark for check
    this._changeDetectorRef.markForCheck();
  }

  orderDate(fieldDate:any):any{
    let date:any = {};
    if(fieldDate !== ''){
      let fecha = this.manilla[fieldDate];
      fecha = fecha.substring(0,10).split('-');
      fecha = fecha[1] + '-' + fecha[2] + '-' + fecha[0];

      let temp = new Date(fecha);

      date = {year: temp.getFullYear(), day: temp.getDate(), month: temp.getMonth() + 1}
    }
    return date;
  }

  fdnToString(fecha:any):any{
    return `${fecha.year}-${fecha.month.toString().padStart(2, '0')}-${fecha.day.toString().padStart(2, '0')}`
  }

  //Files selected
  onFileSelected(event:any, fieldName:any, index:any){
    let fileSelected = event.target.files[0];
    if(fileSelected.type === 'image/jpeg' || fileSelected.type === 'image/png' || fileSelected.type === 'image/jpg' || fileSelected.type === 'application/pdf'){
      const reader = new FileReader();
      reader.readAsDataURL(fileSelected);
      reader.onload = () => {
        let dataN = reader.result.toString();
        // console.log(dataN);
        // let dataD = '';
        // dataD = dataN.replace(/^data:image\/\w+;base64,/,'');
        this.uploadedFiles[fieldName] = dataN;
      };
    }else{
      this.Toast.fire({
        icon: "error",
        title: 'Debe subir un tipo de imagen valido (JPEG, JPG, PNG, PDF).',
      });

      this.uploadedFiles = {};
      this.filesTemp[fieldName] = '';
      // this.manillaForm.get('camposManilla')['controls'][index]['controls'][fieldName].setValue('');
      // this.manillaForm.get(fieldName).setValue('');
      fileSelected = '';
    }
  }

  //submit

  orderData(): any{
    let manilla = this.manillaForm.getRawValue();
    manilla.camposManilla.forEach(element => {
      for (const [key, value] of Object.entries(element)) {
        manilla[key] = value;
      }
    });
    delete manilla.camposManilla;

    this.fieldsList.forEach(field => {
      if(field.type === 'Date'){
        manilla[field.name] = this.fdnToString(manilla[field.name]);
      }
    });

    return manilla;
  }

  deleleteFileFields(data:any):any{ 
    this.fieldsList.forEach(field => {
      if(field.type === 'file'){
        delete data[field.name];
      }
    });

    return data;
  }
  
  onSubmit(){
    if(!this.manillaForm.disabled){
      if (!this.manillaForm.valid) {
        this.Toast.fire({
          icon: 'error',
          title: 'Debe completar los campos requeridos para guardar la informacion'
        });
        document.querySelector('.modal').scrollTo({top:0,behavior:'smooth'});
        // this.disableButton = true;
      } else {
        // this.solicitarManilla();
        this.editarManillaById(this.manilla._id);
      }
    }
    // Mark for check
    this._changeDetectorRef.markForCheck();
  }

  //Pruebas
  pruebas(){
    console.log(this.manillaForm.getRawValue());
  }
}
