import { isPlatformBrowser } from "@angular/common";
import { ChangeDetectorRef, Component, Inject, Input, OnDestroy, OnInit, PLATFORM_ID, TemplateRef, ViewChild } from "@angular/core";
import { AbstractControl, FormBuilder, FormGroup, NgForm, ValidationErrors, ValidatorFn, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { ModalDismissReasons, NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { Gallery, GalleryItem, ImageItem, ImageSize, ThumbnailsPosition } from "ng-gallery";
import { Lightbox } from "ng-gallery/lightbox";
import { Subject, takeUntil } from "rxjs";
import { UserService } from "src/app/core/user/user.service";
import Swal from "sweetalert2";

@Component({
  selector: "app-ver-manilla",
  templateUrl: "./ver-manilla.component.html",
  styleUrls: ["./ver-manilla.component.scss"],
})
export class VerManillaComponent implements OnInit, OnDestroy {

    @ViewChild("modaVerManilla", { static: false }) ModalVerManilla: TemplateRef<any>;
    private _unsubscribeAll: Subject<any> = new Subject<any>();

    public closeResult: string;
    public modalOpen: boolean = false;

    manilla: any = {};

    Toast:any;

    loading: boolean = true;

    items: GalleryItem[];

    fieldsList: any = [];

    showIcons: any = {};

    constructor(
        @Inject(PLATFORM_ID) private platformId: Object,
        private router: Router, private modalService: NgbModal,
        private _formBuilder: FormBuilder,
        private _userService: UserService,
        private _changeDetectorRef: ChangeDetectorRef, public gallery: Gallery, public lightbox: Lightbox
    )
    {
      this.Toast = Swal.mixin({
        toast: true,
        position: 'top-end',
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener('mouseenter', Swal.stopTimer)
          toast.addEventListener('mouseleave', Swal.resumeTimer)
        }
      })    
    }

    ngOnInit(): void {
      this._userService.manillaSelected$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((manilla: any) => {
        this.loading = true;
        // Update the manilla list
        if(manilla !== null && manilla.vista === 'ver'){
          this.getManillaById(manilla.numid);
        }
        // console.log(this.manilla);

        // Mark for check
        this._changeDetectorRef.markForCheck();
      });
    }

    ngOnDestroy() {
      if(this.modalOpen){
        this.modalService.dismissAll();
      }
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next(null);
      this._unsubscribeAll.complete();
    }

    openModal() {
        this.modalOpen = true;
        if (isPlatformBrowser(this.platformId)) { // For SSR 
          this.modalService.open(this.ModalVerManilla, { 
            size: 'lg',
            ariaLabelledBy: 'modal-basic-title',
            centered: true,
            windowClass: 'ModalVerManilla' ,
            scrollable: true
          }).result.then((result) => {
            `Result ${result}`
          }, (reason) => {
          });
        }
        // Mark for check
        this._changeDetectorRef.markForCheck();
    }

    getManillaById(id:any){
      this._userService.getManillaById(id).pipe(takeUntil(this._unsubscribeAll)).subscribe(
        (response:any) => {
          if(response.foto_portador){
            this.items = [new ImageItem({ src: response.foto_portador, thumb: response.foto_portador })];
            // this.items = [new ImageItem({ src: this.imagenTemp, thumb: this.imagenTemp })];
            const lightboxRef = this.gallery.ref('lightbox');
            // Add custom gallery config to the lightbox (optional)
            lightboxRef.setConfig({
              imageSize: ImageSize.Cover,
              thumbPosition: ThumbnailsPosition.Top
            });

            // Load items into the lightbox gallery ref
            lightboxRef.load(this.items);
          }
          this.manilla = response;
          if(response.tipo === 'Mascota'){
            this.manilla.fecha_nacimiento_mascota = this.orderDate(response.fecha_nacimiento_mascota);
          }
          this.getFieldsByType(response.tipo);
        },(error) => {
          this.Toast.fire({
            icon: 'error',
            title: error.error.message
          });
        }
      );
    }

    orderDate(fieldDate:any):any{
      let date:any = {};
      if(fieldDate !== ''){
        let fecha = fieldDate;
        fecha = fecha.substring(0,10).split('-');
        fecha = fecha[1] + '-' + fecha[2] + '-' + fecha[0];
  
        date = new Date(fecha);
      }
      return date;
    }

    getFieldsByType(type: any): void {
      this._userService.getFieldsByUserTypes(type).pipe(takeUntil(this._unsubscribeAll)).subscribe(
          (response: any) => {
            this.fieldsList = response;
            this.loading = false;
          },
          (error) => {
            console.log(error);
          }
        );
    }

    submit(): void{
        
    }

    cambiarEstado(index:any){
      if(this.showIcons['campo'+index]){
        console.log("object");
        this.showIcons['campo'+index] = false;
      }else{
        console.log("2");
        this.showIcons['campo'+index] = true;
      }

      this._changeDetectorRef.markForCheck();

    }
}
